.info {
  width: 100vw;
  height: 100vh;
  background-color: $c-teal;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  padding: 180px 240px;
  clip-path: circle(0%);
}

.info-enter--active {
  animation: 1s cubic-bezier(0.64, 0.14, 0.18, 0.89) reveal-enter forwards;
}

.info-exit--active {
  animation: 1s cubic-bezier(0.64, 0.14, 0.18, 0.89) reveal-exit;
}

@keyframes reveal-enter {
  0% {
    clip-path: circle(0% at 216px calc(100vh - 60px));
  }
  100% {
    clip-path: circle(100%);
  }
}

@keyframes reveal-exit {
  0% {
    clip-path: circle(100%);
  }
  100% {
    clip-path: circle(0% at 216px calc(100vh - 60px));
  }
}

.info__title {
  @include font-surt(48, 60, 800);
  width: 100%;
  margin-bottom: 24px;
}

.info__copy {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-areas: 'type type type type details details';
  grid-column-gap: 80px;

  > p {
    @include font-surt(20, 32, 400);
    grid-area: type;

    > a {
      color: inherit;
    }
  }
}

.info__details {
  grid-area: details;

  h2 {
    @include font-surt(14, 24, 800);
  }

  span:not(.ornament),
  a {
    @include font-surt(14, 24, 400);
    color: inherit;
  }

  .ornament {
    display: block;
    background-color: $c-white;
    height: 4px;
    width: 24px;
    margin: 6px 0 10px 0;
  }

  > li + li {
    margin-top: 32px;
  }
}
