@font-face {
  font-family: 'Surt';
  src: url('./fonts/Surt-Regular.eot') format('embedded-opentype'),
    url('./fonts/Surt-Regular.woff') format('woff'),
    url('./fonts/Surt-Regular.woff2') format('woff2');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Surt';
  src: url('./fonts/Surt-Bold.eot') format('embedded-opentype'),
    url('./fonts/Surt-Bold.woff') format('woff'),
    url('./fonts/Surt-Bold.woff2') format('woff2');
  font-weight: 800;
  font-style: normal;
}

@mixin font-surt(
  $size,
  $line-height,
  $weight: 400,
  $style: normal,
  $text-transform: none
) {
  font-family: 'Surt', Helvetica, Arial, san-serif;
  font-size: $size * 1px;
  font-weight: $weight;
  line-height: $line-height * 1px;
  font-style: $style;
  text-transform: $text-transform;
}
