.button {
  @include unbutton;
  @include font-surt(16, 24, 400);
  display: block;
  width: 40px;
  height: 40px;

  > input {
    @include hide-visually;

    &:checked + span {
      background-color: $c-teal;
    }
  }

  > span {
    border: dashed 2px $c-teal;
    display: block;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  & + .button {
    margin-left: -2px;
  }
}
