body * {
  box-sizing: border-box;
}

html,
body {
  font-family: 'Surt', Helvetica, Arial, sans-serif;
  height: 100%;
  width: 100%;
  font-size: 62.5%;
  margin: 0px;
  color: $c-white;
  background-color: $c-black;
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

fieldset {
  padding: 0;
  margin: 0;
  border: none;
}
