/* * * Header * * * */
.home__stats-container {
  opacity: 1;
  transition: opacity 1000ms ease-out;
}

.home__stats-container--hide {
  opacity: 0 !important;
}

.home__gui {
  position: absolute;
  right: 40px;
  top: 0;

  // HOTFIX: stop background clashing with site background
  .dg li:not(.folder):not(.title) {
    background: #000;
  }
}

/* * * Footer * * * */
.home__foot {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  z-index: 20;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  padding: 40px;

  > fieldset {
    margin-right: 40px;
    display: flex;
  }
}

.fieldset--exit {
  animation: 1s cubic-bezier(0.64, 0.14, 0.18, 0.89) field-exit forwards;
}

.fieldset--enter {
  animation: 1s cubic-bezier(0.64, 0.14, 0.18, 0.89) field-enter forwards;
}

.fieldset--exit--reversed {
  animation: 1s cubic-bezier(0.64, 0.14, 0.18, 0.89) field-exit-reversed
    forwards;
}

.fieldset--enter--reversed {
  animation: 1s cubic-bezier(0.64, 0.14, 0.18, 0.89) field-enter-reversed
    forwards;
}

@keyframes field-exit {
  0% {
    opacity: 1;
    transform: translateX(0%);
  }
  100% {
    opacity: 0;
    transform: translateX(-100%);
  }
}

@keyframes field-enter {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}

@keyframes field-enter-reversed {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}

@keyframes field-exit-reversed {
  0% {
    opacity: 1;
    transform: translateX(0%);
  }
  100% {
    opacity: 0;
    transform: translateX(100%);
  }
}

#info_anchor > span:nth-child(3) {
  opacity: 0;
  border: none;
  position: relative;
  right: 16px;
  bottom: 100%;
}

/* * * Main * * */
.home__canvas {
  width: 100%;
  height: 100%;
}
